<template>
  <div class="infobox" :class="{active: open}">
    <img @click="$emit('close')" :src="closeIcon" alt="svg" class="infobox__close">
    <div class="infobox__block" v-if="current">
      <img v-lazy="imgUrlFull + current.backdrop_path" alt="photo" class="infobox__block-img">
      <div class="infobox__block-content">
        <div class="infobox__block-info">
          <h2 class="infobox__block-info-title">{{ current.title || current.name }}</h2>
          <p class="infobox__block-info-text"> {{ current.overview || 'No description'}}</p>
          <p class="infobox__block-info-descr">
            <span>{{getYear}}{{getGenres}}</span>
            <span>{{getTime}}</span>
          </p>
          <Actors :type="type" :id="current.id" count="4"/>
          <BtnMore :type="type" :id="current.id"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup> 
  import "./InfoBox.scss";
  import closeIcon from '@/assets/images/close.svg'
  import {imgUrlFull} from "@/apiUrl.js";
  import {computed} from "vue";
  import Actors from "@/components/UI/Actors.vue";
  import BtnMore from "@/components/UI/BtnMore.vue";

  const props = defineProps({
    current: Object,
    open: Boolean,
    type: String  
  })

  const getYear = computed(() => new Date( props.current.release_date).getFullYear() ||
  new Date(props.current.first_air_date).getFullYear() + ' ');

  const getGenres = computed(() =>
      props.current.genres.reduce((acc, item) => acc ? acc + ', ' + item.name : item.name, null)
  );

  const getTime = computed(() => {
    if(props.type === 'tv' && props.current.episode_run_time.length == 0 ) {
      return ''
    }
    let minutes =
        props.type === 'movie' ? props.current.runtime :
            props.type === 'tv' ? props.current.episode_run_time[0] : '';

    if (minutes === '') return ''; // minutes bo'sh bo'lsa, hech narsa qaytmaydi.

    let hours = Math.floor(minutes / 60); // Soatlar hisoblanadi.
    let minute = minutes % 60; // Qolgan daqiqalar hisoblanadi.

    if (hours === 0) {
      return `${minute}m`; // Faqat daqiqalar.
    } else {
      return `${hours}h ${minute}m`; // Soat va daqiqalar.
    }
  });


</script>
