<template>
  <div class="actors" v-if="actors">
    <div class="actors__item" v-for="actor in actors" :key="actors.id">
      <img v-lazy="imgUrl + actor.profile_path" alt="">
      <span>{{ actor.name }}</span>
    </div>
  </div>
</template>

<script setup>
import {useActors} from "@/stores/actors.js";
import {computed} from "vue";
import { imgUrl } from "@/apiUrl.js";

const props = defineProps({
    type: String,
    id: Number,
    count: Number
  });

const actorsStore = useActors();
actorsStore.getActors({type: props.type, id: props.id});

const actors = computed(() => {
  let actorsArray =
  props.type == 'movie' ? actorsStore.actorsMovie :
      props.type == 'tv' ? actorsStore.actorsTv : ''
  if(actorsArray) {
    return actorsArray.slice(0,props.count);
  }
})

</script>

