import axios from "axios";
import { apiKey } from "@/apiUrl.js";
import { defineStore } from "pinia";

export const useTop = defineStore('top', {
    state: () => ({
        url: 'https://api.themoviedb.org/3/movie/',
        top: null
    }),
    actions: {
        async getTop() {
            try {
                let response = await axios.get(`${this.url}top_rated?api_key=${apiKey}&language=en-US`);
                this.top = response.data.results
            } catch (error) {
                console.error("Error while retrieving popular movies: " + error.message);
            }
        }
    }
});
