<template>
  <section class="main__video" v-if="content">

    <router-link :to="type" class="main__video-title">
      {{ props.type === 'movie' ? 'Films' : props.type === 'tv' ? 'Serials' : '' }}
      <img :src="arrowIcon" alt="png">
    </router-link>

    <swiper
        :autoplay="{
          delay: 2000,
          disableOnInteraction: false
        }"
        :breakpoints="breakpoints"
        :modules="modules"
        :navigation="true"
        :space-between="25"
        class="main__video-swiper">

      <swiper-slide
          v-for="item in content"
          :key="item.id"
          class="main__video-swiper-item"
          @click="getItem(item)"
      >
        <img
            v-lazy="item.backdrop_path ? imgUrlFull + item.backdrop_path : errorImg"
            alt="photo"
            class="main__video-swiper-item-img"
        />
        <h2 class="main__video-swiper-item-title">{{ item.title }}</h2>
      </swiper-slide>
      <swiper-slide class="main__video-swiper-item">

        <router-link :to="type" class="main__video-swiper-item-page">
          <img :src="arrowIcon" alt="svg">
          {{ props.type === 'movie' ? 'ALL films' : props.type === 'tv' ? 'All serials' : '' }}
        </router-link>

      </swiper-slide>
    </swiper>
    <InfoBox
      :current="current"
      :open="open"
      :type="type"
      @close="close"
    />

  </section>
  <Loader v-else/>
</template>

<script setup>

import './Mat.scss'
import {imgUrlFull} from "@/apiUrl.js";
import arrowIcon from '@/assets/images/arrow.png'
import {usePopular} from "@/stores/popular.js";
import {computed, ref, onMounted, onUnmounted} from "vue";

import {Swiper, SwiperSlide} from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/navigation';
import {Autoplay, Navigation} from 'swiper/modules';
import InfoBox from "@/components/InfoBox/InfoBox.vue";
import Loader from "@/components/UI/Loader.vue";
import {useItemId} from "@/stores/itemId.js";

const props = defineProps({
  type: String
})

const popularStore = usePopular();
popularStore.getPopular({type: props.type});

const content = computed(() => props.type === 'movie' ? popularStore.popularMovies :
    props.type === 'tv' ? popularStore.popularTvs : '')

// swiper settings

const modules = ref([Navigation, Autoplay])

const breakpoints = ref({
  320: {
    slidesPerView: 1
  },
  576: {
    slidesPerView: 2
  },
  900: {
    slidesPerView: 3
  },
  1200: {
    slidesPerView: 4
  },
  1400: {
    slidesPerView: 5
  },
})

// Dobavleniye funksiyonala pri klike

let itemIdStore = useItemId();
let current = ref(null);
let open = ref(false);
const getItem = async (item) => {
  if(disableClick.value) {
    return
  }
  current.value = null
  await itemIdStore.getItemId({type: props.type, id: item.id})
  current.value = props.type === 'movie' ? itemIdStore.movie : props.type === 'tv' ?
      itemIdStore.tv : '';
  open.value = true
}

const close = () => {
  open.value = false
}

const disableClick = ref(false)

const checkSize = () => {
  const screenWidthThreshold = 800
  disableClick.value = window.innerWidth < screenWidthThreshold;
}

onMounted((() => {
  window.addEventListener('resize', checkSize);
  checkSize();
}))

onUnmounted(() => {
  window.removeEventListener('resize', checkSize);
}),
    window.removeEventListener('resize', checkSize);

</script>
