import axios from "axios";
import { apiKey } from "@/apiUrl.js";
import { defineStore } from "pinia";

export const useUpcoming = defineStore('upcoming', {
    state: () => ({
        url: 'https://api.themoviedb.org/3/movie/upcoming',
        upcoming: null
    }),
    actions: {
        async getUpcoming() {
            try {
                let response = await axios.get(`${this.url}?api_key=${apiKey}&language=en-US`);
                this.upcoming = response.data.results; // Ma'lumotni saqlash
            } catch (error) {
                console.error("Error while retrieving upcoming movies: " + error.message);
            }
        }
    }
});
