import axios from "axios";
import {defineStore} from "pinia";
import {apiKey} from "@/apiUrl.js";

export const useItemId = defineStore('itemId', {
    state: () => ({
        url: 'https://api.themoviedb.org/3/',
        movie: null,
        tv: null
    }),
    actions: {
        async getItemId({type, id}) {
            try {
                let res = await axios.get(`${this.url}${type}/${id}?api_key=${apiKey}&language=en-US`)
                console.log(res.data); // bu yerda ham tekshiring
                if(type === 'movie') {
                    this.movie = res.data
                } else if (type === 'tv') {
                    this.tv = res.data
                }
            } catch (error) {
                console.error("An error occurred while retrieving information by ID." + error);
            }
        }
    }
});
