<template>
  <div class="main__upcoming" v-if="upcomingMovies">
    <UpcomingItem
      v-for="(movie, i) in upcomingMovies"
      :key="movie.id"
      :movie="movie"
      :i="i"
      :activeItem="activeItem"
      :nextMovie="upcomingMovies[i + 1] || upcomingMovies[0]"
      @nextSlide="nextSlide"
    />
    <!-- activeItem - bu keladigan yacheykalar -->
    <!-- :movie, :i, :activeItem bular propslar, ularni boshqa componentda ishlatish uchun-->
  </div>
  <Loader v-else/>
</template>

<script setup>

import UpcomingItem from "@/components/Upcoming/UpcomingItem.vue";
import {useUpcoming} from "@/stores/upcoming.js"; /* upcoming.js piniani import qilib olamiz */
import { computed, ref, onMounted } from "vue";
import Loader from "@/components/UI/Loader.vue";

const upcomingStore = useUpcoming(); /* Ulanayabmiz state'ga */
upcomingStore.getUpcoming(); /* actions'dagi getUpcoming'ni yurgizamiz */
// setTimeout(() => {
//  upcomingStore.getUpcoming()  /* Bu loading ko'rinishi uchun ozroq vaqt beradi. */
// }, 5000);
const upcomingMovies = computed(() => upcomingStore.upcoming);

let activeItem = ref(0);
let timeout = ref(null);

const next = () => {
  if(upcomingMovies.value.length - 1 == activeItem.value) {
    activeItem.value = 0
  } else {
    activeItem.value++
  }
  timeout = setTimeout(() => next(), 5000);
}

const nextSlide = () => {
  clearTimeout(timeout);
  next();
}

onMounted(() => {
  timeout = setTimeout(() => next(), 5000);
})
</script>

<style src="./Upcoming.scss">

</style>
